*{
  margin: 0;padding: 0;
  box-sizing: border-box;
}

.navbar{
  display: flex;
  list-style: none;
  justify-content: space-around;
  font-size: 150%;
  color:#08787c;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.coder{
  color: #c78bff;
  text-align: center;
  margin-top: 2rem;
}

.navbar-brand {
  font-family: 'Foldit', cursive;
  font-size: 2rem;
  font-weight: 700;
  text-shadow: 1px .25px .5px red, 0 0 .1em blue, 0 0 0.1em blue;
}
